body {
  background-color: #e2e2e2;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

h1::selection, h2::selection, h3::selection, h4::selection, h5::selection, h6::selection, p::selection, a::selection, span::selection {
  background-color: #D0EEE6;
}

[class$=contenedor],
.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.seccion {
  padding: 8rem 0;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
}

h1 {
  font-size: 4.5rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

[class$=heading] {
  margin: 1rem 0;
}

[class$=highlight] {
  color: #feac25;
  font-weight: 600;
}

a {
  text-decoration: none;
}

.btn {
  background-color: #FEC362;
  padding: 1.2rem 2rem;
  border-radius: 20rem;
  border: 2px solid #222726;
  color: #222726;
  font-size: 2rem;
  font-weight: 600;
  display: inline-block;
  text-align: center;
  transition: all 300ms;
}
@media (min-width: 768px) {
  .btn {
    width: auto;
  }
}
.btn:hover {
  background-color: #feb02f;
  scale: 0.98;
}

.boton-negro {
  background-color: #222726;
  padding: 1.2rem 2rem;
  border-radius: 20rem;
  border: 2px solid #222726;
  color: #FFFFFF;
  font-size: 2rem;
  font-weight: 600;
  display: inline-block;
  text-align: center;
  transition: all 300ms;
}
@media (min-width: 768px) {
  .boton-negro {
    width: auto;
  }
}
.boton-negro:hover {
  background-color: #0a0c0b;
  scale: 0.98;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #ffaba9;
  height: 7rem;
  z-index: 100;
}

.navM {
  height: 7rem;
}
.navM__data {
  height: 100%;
  padding: 0 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 2px solid #222726;
}
.navM__logo {
  width: 7rem;
}
.navM__toggle {
  position: relative;
  width: 3rem;
  height: 3rem;
  /* background-color: darken(v.$yellow4, 1%);
  border-radius: .5rem;
  box-shadow:  2px 2px 7px 0px rgba(0, 0, 0, 0.50); */
}
.navM__burger, .navM__close {
  position: absolute;
  width: max-content;
  height: max-content;
  margin: auto;
  inset: 0;
  font-size: 2.5rem;
  cursor: pointer;
  transition: opacity 0.1s, transform 0.4s;
}
.navM__close {
  opacity: 0;
}
.navM__menu {
  position: absolute;
  left: 0;
  top: 7rem;
  width: 100%;
  height: calc(100vh - 3.5rem);
  overflow: auto;
  pointer-events: none;
  opacity: 0;
  transition: top 0.4s, opacity 0.3s;
}
.navM__menu::-webkit-scrollbar {
  width: 0;
}
.navM__list {
  background-color: rgba(165, 223, 208, 0.9);
  padding: 2rem 0;
  box-shadow: 2px 0 7px 0px rgba(0, 0, 0, 0.5);
  border-bottom: 2px solid #222726;
}
.navM__item {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.navM__icon {
  font-size: 2.2rem;
}
.navM__link {
  color: #222726;
  font-weight: 600;
  padding: 1.25rem 1.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: background-color 0.3s;
}
.navM__link:hover {
  background-color: #a6dfd1;
}

.show-menu {
  opacity: 1;
  top: 7rem;
  pointer-events: initial;
}

.show-icon .navM__burger {
  opacity: 0;
  transform: rotate(90deg);
}

.show-icon .navM__close {
  opacity: 1;
  transform: rotate(90deg);
}

