.hero {
  margin-top: 7rem;
  padding: 2rem 2rem 3rem 2rem;
  background: linear-gradient(166deg, #FFF3F2 40%, #FFC1C0 100%);
  display: flex;
  flex-direction: column;
}
.hero__heading {
  font-weight: 600;
  text-align: center;
}
.hero__text {
  font-size: 1.5rem;
  line-height: 1.7;
  text-align: center;
}
.hero__boximg {
  margin: 2rem auto 4rem auto;
  position: relative;
  max-height: 32rem;
}
.hero__imgMarco {
  width: 25rem;
  height: 25rem;
  border: 10px solid #FEC362;
  border-radius: 2rem;
  margin: 0 2rem;
}
.hero__img {
  width: 25rem;
  height: 25rem;
  position: absolute;
  top: 1.5rem;
  left: 0;
  object-fit: cover;
  border-radius: 2rem;
  box-shadow: 3px 4px 7px 0px rgba(0, 0, 0, 0.5);
}
.hero__btn {
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  width: 100%;
  max-width: 50rem;
}
.hero__btnIcono {
  font-size: 2rem;
}

.atributos {
  padding: 2.5rem 2rem 2.5rem 2rem;
  background-color: #EFF9F7;
}
.atributos__heading, .atributos__texto {
  max-width: 50rem;
  margin: 0 auto;
}
.atributos__heading {
  margin: 0 auto;
  text-align: center;
  border-bottom: 5px solid #FEC362;
}
.atributos__texto {
  padding: 2rem 0;
  text-align: center;
}
.atributos__widgets {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  row-gap: 2rem;
}

.widget {
  border: 2px solid #222726;
  border-radius: 1rem;
  background-color: #81D1BD;
  box-shadow: 3px 4px 7px 0px rgba(0, 0, 0, 0.5);
  padding: 1.5rem 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  width: 100%;
  max-width: 37rem;
}
.widget__icon {
  font-size: 3.7rem;
}
.widget__heading, .widget__text {
  text-align: center;
}
.widget__heading {
  font-weight: 600;
  font-size: 2rem;
}
.widget__text {
  font-size: 1.5rem;
}

.testimoniales {
  background-color: #FFF8EB;
  padding: 2.5rem 2rem 2.5rem 2rem;
}
.testimoniales__heading, .testimoniales__texto {
  max-width: 40rem;
  margin: 0 auto;
}
.testimoniales__heading {
  margin: 0 auto;
  text-align: center;
  border-bottom: 5px solid #FEC362;
}
.testimoniales__texto {
  padding: 2rem 0rem;
  text-align: center;
}
.testimoniales__widgets {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  row-gap: 2rem;
}

.widget2 {
  border: 2px solid #222726;
  border-radius: 1rem;
  background-color: #FEC362;
  box-shadow: 3px 4px 7px 0px rgba(0, 0, 0, 0.5);
  padding: 2rem 2rem;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-around;
  row-gap: 2rem;
  max-width: 37rem;
}
.widget2__iconos, .widget2__texto, .widget2__cliente {
  width: 100%;
}
.widget2__texto {
  font-size: 1.5rem;
}
.widget2__cliente {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  gap: 1rem;
}
.widget2__img {
  width: 4.2rem;
  height: 4.2rem;
  object-fit: cover;
  border-radius: 50%;
}
.widget2__posicion {
  font-size: 1.2rem;
}

.faq {
  background-color: #FFF3F2;
  padding: 2rem 2rem;
}
.faq__heading, .faq__texto {
  max-width: 40rem;
  margin: 0 auto;
}
.faq__heading {
  margin: 0 auto;
  text-align: center;
  border-bottom: 5px solid #FEC362;
}
.faq__texto {
  padding: 2rem 0rem;
  text-align: center;
}
.faq__widgets {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  row-gap: 2rem;
}
.faq__pregunta {
  text-align: center;
  padding-top: 2rem;
}

.accordion {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  row-gap: 2rem;
}
.accordion__content {
  background-color: #FFC1C0;
  border: 2px solid #222726;
  border-radius: 1rem;
  box-shadow: 3px 4px 7px 0px rgba(0, 0, 0, 0.5);
  padding: 2rem 2rem;
  max-width: 37rem;
  width: 100%;
  overflow: hidden;
}
.accordion .open {
  padding-bottom: 1rem;
}
.accordion__question {
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  /* min-height: 5rem; */
  /* padding: 0 1.5rem; */
  transition: all 0.2s linear;
  column-gap: 1rem;
}
.accordion__title {
  font-size: 1.7rem;
}
.accordion__icon {
  font-size: 2rem;
}
.accordion__description {
  height: 0;
  font-size: 1.4rem;
  padding: 0 1.5rem;
  color: #3a4241;
  transition: all 0.2s linear;
  line-height: 1.4;
  opacity: 0;
}

.footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  background-color: #ffaba9;
  border-top: 2px solid #222726;
  row-gap: 1rem;
  padding: 1.5rem 0;
}
.footer__logo {
  width: 10rem;
}
.footer__social {
  display: flex;
  flex-direction: row;
  justify-content: center;
  column-gap: 2rem;
}
.footer__socialLink {
  color: #222726;
  font-size: 2.5rem;
}
.footer__links {
  width: 100%;
  margin: 0 auto;
  padding: 2rem;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  align-items: center;
  max-width: 768px;
}
.footer__link {
  color: #222726;
  text-decoration: underline;
  font-size: 1.3rem;
  text-align: center;
}
.footer__line {
  width: 85%;
  border: 1px solid #222726;
  max-width: 768px;
}
.footer__copyright {
  text-align: center;
  font-size: 1.3rem;
  padding: 0.5rem 2rem;
}